"use strict";
import THREE from 'three';
import OrbitControls from './OrbitControls'

const uniforms = {
    opacity: { type: "f", value: 1.0 }
};

const shaderMaterial = new THREE.ShaderMaterial({
    uniforms: uniforms,
    vertexShader: document.getElementById('vertexshader').textContent,
    fragmentShader: document.getElementById('fragmentshader').textContent,
    blending: THREE.AdditiveBlending,
    depthTest: false,
    transparent: true
});


export class Viewer {
    constructor() {
        this._scene = new THREE.Scene();
        this._camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 800);
        this._camera.position.z = 10;
        
        this._controls = new OrbitControls(camera);
        this._controls.addEventListener('change', render);
        this._controls.enableDamping = true;
        this._controls.dampingFactor = 0.25;
        this._controls.enableZoom = true;

        this._renderer = new THREE.WebGLRenderer();
        this._renderer.setSize(window.innerWidth, window.innerHeight);
        document.body.appendChild(this._renderer.domElement);
    }

    drawEvents(events) {
        const geometry = new THREE.Geometry()
        for (let event of events) {
            const killer = event.KillerWorldLocation;
            const victim = event.VictimWorldLocation;

            geometry.vertices.push(
                new THREE.Vector3(killer.x, killer.y, killer.z),
                new THREE.Vector3(victim.x, victim.y, victim.z));
        }

        const buffergeometry = new THREE.BufferGeometry();

        const customColor = new THREE.Float32Attribute(events.length * 3 * 2, 3);
        buffergeometry.addAttribute('customColor', customColor);

        for (let i = 0, l = customColor.count; i < l;) {
            new THREE.Color(0xff00ff).toArray(customColor.array, i * customColor.itemSize);
            ++i
            new THREE.Color(0x00ffff).toArray(customColor.array, i * customColor.itemSize);
            ++i;
        }

        const position = new THREE.Float32Attribute(geometry.vertices.length * 3, 3).copyVector3sArray(geometry.vertices);
        buffergeometry.addAttribute('position', position)

        const line = new THREE.LineSegments(buffergeometry, shaderMaterial);
        this._scene.add(line);
        this.animate();
        this.render();
        return null;
    };

    animate() {
        this._controls.update();
        this.render();
    }

    render() {
        this._renderer.render(this._scene, this._camera);
    }
}


